.Home{
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
    min-width: 100%;
    height: 620px;
}
.slide{
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 800px;
    position: relative;
    z-index: -1;

}
.slide img{
    width: 100%;
    height: 600px
}

.topOfCarousel {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    background-color: #14477b;
    color: white;
    padding: 30px;
    justify-content: center;
}



.secondSlide {
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
    min-height: 800px;
    z-index: -1;
}

.secondSlide img {
    width: 100%
}

.btnStyle {
    background: #14477b !important;
    padding: 14px !important;
    font-weight: 'bold';
    border-color: none !important;
}

